	module.exports = {"data":{"markdownRemark":{"html":"<!---\nThis is an autogenerated file!\n\nPlease do not edit this file directly, but instead make changes to the\nsigs.yaml file in the project root.\n\nTo understand how this file is generated, see https://git.k8s.io/community/generator/README.md\n-->\n<h1>Storage Special Interest Group</h1>\n<p>Covers storage and volume plugins.</p>\n<h2>Meetings</h2>\n<ul>\n<li>\n<p>Regular SIG Meeting: <a href=\"https://zoom.us/j/614261834\">Thursdays at 9:00 PT (Pacific Time)</a> (biweekly). <a href=\"http://www.thetimezoneconverter.com/?t=9:00&#x26;tz=PT%20%28Pacific%20Time%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/1-8KEG8AjAgKznS9NFm3qWqkGyCHmvU6HVl0sk5hwoAE/edit?usp=sharing\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://www.youtube.com/watch?v=Eh7Qa7KOL8o&#x26;list=PL69nYSiGNLP02-BMqJdfFgGxYQ4Nb-2Qq\">Meeting recordings</a>.</li>\n</ul>\n</li>\n</ul>\n<h2>Leadership</h2>\n<h3>Chairs</h3>\n<p>The Chairs of the SIG run operations and processes governing the SIG.</p>\n<ul>\n<li>Saad Ali (<strong><a href=\"https://github.com/saad-ali\">@saad-ali</a></strong>), Google</li>\n<li>Bradley Childs (<strong><a href=\"https://github.com/childsb\">@childsb</a></strong>), Red Hat</li>\n</ul>\n<h2>Contact</h2>\n<ul>\n<li><a href=\"https://kubernetes.slack.com/messages/sig-storage\">Slack</a></li>\n<li><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-storage\">Mailing list</a></li>\n<li><a href=\"https://github.com/kubernetes/community/labels/sig%2Fstorage\">Open Community Issues/PRs</a></li>\n</ul>\n<h2>Subprojects</h2>\n<p>The following subprojects are owned by sig-storage:</p>\n<ul>\n<li>\n<p><strong>external-storage</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes-incubator/external-storage/master/OWNERS\">https://raw.githubusercontent.com/kubernetes-incubator/external-storage/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>git-sync</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/git-sync/master/OWNERS\">https://raw.githubusercontent.com/kubernetes/git-sync/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>nfs-provisioner</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes-incubator/nfs-provisioner/master/OWNERS\">https://raw.githubusercontent.com/kubernetes-incubator/nfs-provisioner/master/OWNERS</a></li>\n</ul>\n</li>\n<li>\n<p><strong>volumes</strong></p>\n<ul>\n<li>Owners:</li>\n<li><a href=\"https://raw.githubusercontent.com/kubernetes/kubernetes/master/pkg/volume/OWNERS\">https://raw.githubusercontent.com/kubernetes/kubernetes/master/pkg/volume/OWNERS</a></li>\n</ul>\n</li>\n</ul>\n<h2>GitHub Teams</h2>\n<p>The below teams can be mentioned on issues and PRs in order to get attention from the right people.\nNote that the links to display team membership will only work if you are a member of the org.</p>\n<p>The google groups contain the archive of Github team notifications.\nMentioning a team on Github will CC its group.\nMonitor these for Github activity if you are not a member of the team.</p>\n<table>\n<thead>\n<tr>\n<th>Team Name</th>\n<th align=\"center\">Details</th>\n<th align=\"center\">Google Groups</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@kubernetes/sig-storage-api-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-storage-api-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-storage-api-reviews\">link</a></td>\n<td>API Changes and Reviews</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-storage-bugs</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-storage-bugs\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-storage-bugs\">link</a></td>\n<td>Bug Triage and Troubleshooting</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-storage-feature-requests</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-storage-feature-requests\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-storage-feature-requests\">link</a></td>\n<td>Feature Requests</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-storage-misc</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-storage-misc\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-storage-misc\">link</a></td>\n<td>General Discussion</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-storage-pr-reviews</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-storage-pr-reviews\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-storage-pr-reviews\">link</a></td>\n<td>PR Reviews</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-storage-proposals</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-storage-proposals\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-storage-proposals\">link</a></td>\n<td>Design Proposals</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-storage-test-failures</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-storage-test-failures\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-storage-test-failures\">link</a></td>\n<td>Test Failures and Triage</td>\n</tr>\n</tbody>\n</table>\n<!-- BEGIN CUSTOM CONTENT -->\n<h2>Details</h2>\n<p>The Kubernetes Storage Special-Interest-Group (SIG) is a working group within the Kubernetes contributor community interested in storage and volume plugins.</p>\n<h3>Meeting</h3>\n<p>We hold a public meeting every two weeks, on Thursdays at 9 AM (PT).</p>\n<ul>\n<li>The meeting is held on Zoom: <a href=\"https://zoom.us/j/614261834\">https://zoom.us/j/614261834</a></li>\n<li>Agenda and any notes from each meeting are captured in <a href=\"https://docs.google.com/document/d/1-8KEG8AjAgKznS9NFm3qWqkGyCHmvU6HVl0sk5hwoAE/edit?usp=sharing\">this document</a>.</li>\n<li>Contact saadali@google.com to be added to the calendar invite.</li>\n<li>Recordings of past meetings: <a href=\"https://www.youtube.com/playlist?list=PLb1_clREIGYVaqvKaUsVyXxjfcSQDBnmB\">https://www.youtube.com/playlist?list=PLb1_clREIGYVaqvKaUsVyXxjfcSQDBnmB</a></li>\n</ul>\n<h3>Contributing</h3>\n<p>Interested in contributing to storage features in Kubernetes? <a href=\"https://github.com/kubernetes/community/blob/master/sig-storage/contributing.md\">Please read our guide for new contributors</a></p>\n<h3>Links</h3>\n<ul>\n<li>\n<p>Public Slack Channel: <a href=\"https://kubernetes.slack.com/messages/sig-storage/details/\">https://kubernetes.slack.com/messages/sig-storage/details/</a></p>\n<ul>\n<li>Get invite to join here: <a href=\"http://slack.k8s.io/\">http://slack.k8s.io/</a></li>\n<li>Our channel is <code>sig-storage</code></li>\n</ul>\n</li>\n<li>\n<p>Github Issues</p>\n<ul>\n<li><a href=\"https://github.com/kubernetes/kubernetes/issues?q=is%3Aopen+is%3Aissue+label%3Asig%2Fstorage\">link</a></li>\n</ul>\n</li>\n<li>Documentation for currently supported volume plugins: <a href=\"https://kubernetes.io/docs/concepts/storage/volumes/\">https://kubernetes.io/docs/concepts/storage/volumes/</a></li>\n<li>\n<p>Code</p>\n<ul>\n<li><a href=\"https://github.com/kubernetes/kubernetes/tree/master/pkg/volume\">Volume plugins</a></li>\n<li><a href=\"https://github.com/kubernetes/kubernetes/tree/master/pkg/controller/volume/\">Volume controllers</a></li>\n<li><a href=\"https://github.com/kubernetes/kubernetes/blob/master/pkg/kubelet/volumemanager/\">Kubelet volume manager</a></li>\n</ul>\n</li>\n</ul>\n<!-- END CUSTOM CONTENT -->"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-storage/"}}

